<template>
  <div id="item" @click="itemClick">
    <div class="icon" v-show="!isActive">
      <slot name="icon"></slot>
    </div>
    <div class="icon-active" v-show="isActive">
      <slot name="icon-active"></slot>
    </div>
    <div class="text" :style="activeStyle">
      <slot name="text"></slot>
    </div>
  </div>
</template>
<script>
export default {
  name: 'TabBarItem',
  components: {},
  data() {
    return {
      
    }
  },
  props: {
    link: {
      type: String
    },
    color: {
      type: String,
      default: 'red'
    }
  },
  computed: {
    isActive(){
      return this.$route.path.indexOf(this.link) !== -1
    },
    activeStyle(){
      return this.isActive ? {color: this.color} : {}
    }
  },
  methods: {
    itemClick(){
      this.$router.replace(this.link)
    }
  }
}
</script>
<style scoped>
#item {
  flex: 1;
}
#item img {
  width: 24px;
  height: 24px;
  vertical-align: center;
  margin-top: 2px;
}

#item .text {
  font-size: 12px;
  margin-top: 2px;
  color: #333;
}
</style>